import { Component, OnInit } from '@angular/core';
import { UserManageBean } from './bean/user.manage.bean';
import { pageSBean } from './bean/pageS.bean'
import { CommonHttpService } from '../../http/common.http.service';
import { ConfirmationService } from 'primeng/primeng';
import { Message } from 'primeng/primeng'
// import { TellerStatus } from '../../constant/common.codeValue';
import { Commfunc } from 'app/@uisftech/common/service/commonFun'
import { tableMessage } from 'app/@uisftech/common/constant/config';
import value from '*.json';


@Component({
  selector: 'user-manage',
  templateUrl: './user-manage.component.html',
  styleUrls: ['./user-manage.component.scss'],
  providers: [ConfirmationService]
})
export class UserManagePage implements OnInit {
  org: any;
  cities: any;
  // 表格中显示的信息
  tabMesg: any = tableMessage;
  precredit: string = '用户管理';
  // 表体内容
  tableData: any;
  tableData1: any
  userManageBean: UserManageBean = new UserManageBean()
  pageSBean: pageSBean = new pageSBean()

  //总页数
  total: string;
  //每页多少条
  pageSize: string = '10';

  //提示信息
  msgs: Message[] = [];

  // 所属机构
  status: any = [];
  // 员工状态
  opera: any = [];
  //模态框的标题
  headerTitle: string = "柜员同步";
  //模态框是否显示
  display: boolean = false;
  //模态框
  showModel: string;
  //传出的值
  inValue: string;
  //接收机构名称
  list: any[];
  //接收机构id  
  list1: any[];
  //修改的参数
  updateValue: any = [];
  tellerStatus: any[] = [];
  //转模糊查询
  orgName: string;
  orgId: string;
  //当前页
  currentPage: string;
  rows: any;
  first: number = 0;
  bb: any;
  inpFlag: boolean = true;//模糊查询
  code: any;
  constructor(
    public commonHttpService: CommonHttpService,
    public confirmationService: ConfirmationService,
    public commfunc: Commfunc
  ) {
    this.codeValues() //调用方法，获取全部码值
    this.tellerStatus = this.code['TellerStatus'];
  }

  ngOnInit() {
    this.userManageBean = new UserManageBean();
    this.userManageBean.pageSize = '10';
    this.userManageBean.pageNum = '1';
    this.orgquery('', '');
    //按回车键查询
    let _thiss = this;
    document.onkeydown = function (event) {
      if (event && event.keyCode === 13) {
        _thiss.query();
      }
    }
  }
  //码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }
  ngOnChanges() {
    this.ngOnInit();

  }

  // //机构模糊查询
  inpquery($event) {
    if (this.inpFlag) {
      this.inpFlag = false;
      setTimeout(() => {
        this.inpFlag = true;
        let orgId = document.getElementById('orgId').getElementsByTagName('input')[1].value
        let orgName = document.getElementById('orgName').getElementsByTagName('input')[1].value
        // this.orgquery($event.path[0].value);
        this.orgquery(orgId, orgName)
      }, 1000)
    }
  }
  orgquery(orgId, orgName) {
    // this.org = JSON.parse(this.commfunc.getSessionData('officeOrgId'));
    let param = {
      // orgId:/\w/.test(event)?event:'',
      // orgName:/\w/.test(event)?'':event,
      orgId: orgId,
      orgName: orgName,
      pageSize: '30'
    }
    this.commonHttpService.queryVagueOrgList(param).subscribe(data => {
      if (data.orgList) {
        this.list = this.commfunc.listToLabelvalue(data.orgList, 'orgName', 'orgId');
        this.list1 = this.commfunc.listToLabelvalue(data.orgList, 'orgId', 'orgName');
        this.queryClick();
      }
    })
  }

  //onchange事件
  doChose(event) {
    this.userManageBean.orgName = this.returnValue(event.value, 'label', 'value');
  }
  doChose1(event) {
    this.userManageBean.orgId = this.returnValue(event.value, 'value', 'label');
  }

  /**
* 转换值
* @param param  值
* @param colum  字段
*/
  returnValue(param, colum, valueColum) {
    let value: string = "";
    this.list.forEach(element => {
      if (element[colum] === param) {
        value = element[valueColum];
      }
    });
    return value;
  }

  //查询
  queryClick() {
    let param = {
      tellerName: this.userManageBean.tellerName,
      tellerId: this.userManageBean.tellerId,
      orgId: this.userManageBean.orgName,
      orgName: this.userManageBean.orgId,
      tellerStatus: this.userManageBean.tellerStatus,
      pageNum: this.userManageBean.pageNum,
      pageSize: this.userManageBean.pageSize
    };
    this.commonHttpService.queryTellerByCombConditionsLevel(param).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.tableData = data.tellerList;
        this.total = data.total;
        this.msgs = [];
        this.msgs.push({ severity: "success", summary: '提示', detail: data.returnCode.message })
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message});
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
  }
  query() {
    this.first = 0;
    this.userManageBean.pageNum = '1';
    let param = {
      tellerName: this.userManageBean.tellerName,
      tellerId: this.userManageBean.tellerId,
      orgId: this.userManageBean.orgName,
      orgName: this.userManageBean.orgId,
      tellerStatus: this.userManageBean.tellerStatus,
      pageNum: this.userManageBean.pageNum,
      pageSize: this.userManageBean.pageSize
    };
    this.commonHttpService.queryTellerByCombConditionsLevel(param).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.tableData = data.tellerList;
        this.total = data.total;
        this.msgs = [];
        this.msgs.push({ severity: "success", summary: '提示', detail: data.returnCode.message })
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
  }


  // 重置
  reset() {
    this.userManageBean.tellerName = '';
    this.userManageBean.tellerId = '';
    this.userManageBean.tellerStatus = '';
    this.userManageBean.orgId = '';
    this.userManageBean.orgName = '';
  }

  // 增加
  followMe() {
    this.headerTitle = '柜员同步';
    this.display = true;
    this.showModel = '1';
  }

  //添加后返回的值
  addCall(param) {
    this.display = param;
    // this.userManageBean.pageNum = '1';
    this.queryClick();
  }

  //分页
  paginate(event) {
    this.rows = event.rows + '';
    //每页显示的条数
    this.userManageBean.pageSize = this.rows;
    //当前页
    this.currentPage = event.page + 1 + '';
    this.userManageBean.pageNum = this.currentPage;
    this.first = event.rows * event.page
    //调用查询的方法
    this.queryClick();
    // this.userManageBean.pageNum = '1';
  }

  //修改
  update(item) {
    this.headerTitle = '修改柜员信息';
    this.display = true;
    this.showModel = '2';
    this.updateValue = item;
  }

  //修改的回调
  updateCall(param) {
    this.display = param;
    this.userManageBean.pageNum = this.currentPage;
    this.queryClick();
  }
  //按钮权限
  permissionCheck(id) {
    return this.commfunc.permissionCheck(id)
  }
}
