import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { UserManageBean } from '../bean/user.manage.bean';
import { CommonHttpService } from '../../../http/common.http.service';
import { ConfirmationService } from 'primeng/primeng';
import { Message } from 'primeng/primeng'
// import { TellerStatus } from '../../../constant/common.codeValue';
import { Commfunc } from 'app/@uisftech/common/service/commonFun'
@Component({
  selector: 'app-user-message-update',
  templateUrl: './user-message-update.component.html',
  styleUrls: ['./user-message-update.component.scss'],
  providers: [Commfunc, ConfirmationService]
})
export class UserMessageUpdateComponent implements OnInit {
  @Input() inValue: any;
  @Output() outValue: EventEmitter<any> = new EventEmitter();
  userManageBean: UserManageBean = new UserManageBean();
  tellerStatus: any = [];
  list: any;
  list1: any;
  userform: FormGroup; //校验        
  //提示信息
  msgs: Message[] = [];
  //接收父级传回的数据
  userManage: any;
  a: string;//接收修改时机构下拉的值便于转换
  b: string;//接收修改时机构下拉的值便于转换
  param: any;
  orgFlag: boolean = false;
  departmentFlag: boolean = false;
  teamFlag: boolean = false;
  orgId: string;//修改专用
  orgName: string;//修改专用
  departmentId: any;
  departmentName: any;
  team: any;
  teamId: any;
  teamName: any;
  orgId1: string;
  orgName1: string;
  departmentId1: any;
  departmentName1: any;
  teamId1: any;
  teamName1: any;
  operatingLevel: any;
  orgList: any;
  bb: any;
  inpFlag: boolean = true;//机构模糊查询
  code: any;
  constructor(
    public commonHttpService: CommonHttpService,
    public confirmationService: ConfirmationService,
    public commfunc: Commfunc,
    public fb: FormBuilder,
  ) {
    this.codeValues() //调用方法，获取全部码值
    this.tellerStatus = this.code['TellerStatus'];

  }

  ngOnInit() {
    this.orgquery('');
    this.userform = this.fb.group({
      'orgId': new FormControl('', [Validators.required]),
      'tellerName': new FormControl('', [Validators.required]),
      // 'roleDescribe':new FormControl('',[Validators.pattern(/^[\u4e00-\u9fa5a-zA-Z0-9\u3002\uff01\uff0c\u3001\uff1b\uff1a\u201c\u201d\u2018\u2019\uff08\uff09\u300a\u300b\u300b\u3008\u3009\u3010\u3011\u300e\u300f\u300c\u300d\ufe43\ufe44\u3014]{1,255}$/),Validators.required])
      'orgName': new FormControl('', Validators.required),
      'email': new FormControl('', [Validators.pattern(/^([\w\.-]+@([\w-]+\.)[a-zA-Z]{2,3}|[\u65e0]?)$/)]),
      'mobileNum': new FormControl('', [Validators.pattern(/^[0-9]{11}$/)]),
      "tellerStatus": new FormControl('', Validators.required),
      "tellerId": new FormControl('', Validators.required),
      "departmentId": new FormControl('', ),
      "departmentName": new FormControl('', ),
      "teamName": new FormControl('', ),
      "teamId": new FormControl('', ),
    });
    // this.userManageBean = Object.assign({}, this.inValue);
    this.userManageBean = JSON.parse(JSON.stringify(this.inValue))
    this.orgId = this.inValue.orgId;
    this.orgName = this.inValue.orgName;
    // this.userManageBean.orgName = this.orgId;
    // this.userManageBean.orgId = this.orgName;
    
  }
  //码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }

  // ngOnChanges() {
  //   this.fuzhi();
  // }
  // fuzhi() {
    

  // }

  //机构模糊查询
  inpquery($event) {
    if (this.inpFlag) {
      this.inpFlag = false;
      setTimeout(() => {
        this.inpFlag = true;
        let org = document.getElementById('org').getElementsByTagName('input')[2].value;
        this.orgquery(org)
        // this.orgquery($event.path[0].value)
      }, 1000)
    }
  }


  orgquery(event) {
    let param = {
      orgId: event,
      pageSize: '30'
    }
    this.commonHttpService.queryVagueOrgList(param).subscribe(data => {
      if (data.orgList) {
        this.orgList = data.orgList
        this.list = this.commfunc.listToLabelvalue(data.orgList, 'orgName', 'orgId');
        this.list1 = this.commfunc.listToLabelvalue(data.orgList, 'orgId', 'orgName');
      }
    })
  }

  //所属部门标号模糊查询
  queryTeamId() {
    let param = {
      orgId: /\w/.test(this.userManageBean.orgId) ? this.userManageBean.orgId : this.userManageBean.orgName,
      relationshipType: "-1",
      operatingLevel: this.operatingLevel
    }
    this.commonHttpService.queryOrgByOrgIdAndRelationship(param).subscribe(data => {
      if (data.orgList) {
        // this.team = data.orgList;
        this.departmentId = this.listToLabelvalue(data.orgList, 'orgId', 'orgName').departmentArr;
        this.departmentName = this.listToLabelvalue(data.orgList, 'orgName', 'orgId').departmentArr;
        this.teamId = this.listToLabelvalue(data.orgList, 'orgId', 'orgName').teamArr;
        this.teamName = this.listToLabelvalue(data.orgList, 'orgName', 'orgId').teamArr;
      }
    })
  }

  //后台返回变成下拉框的值
  listToLabelvalue(data: any = [], label, value) {
    let tempObj = {}
    let departmentArr1 = [];
    let teamArr1 = [];
    if (data) {
      data.forEach(item => {
        let tempObj = {};
        tempObj['label'] = item[label];
        tempObj['value'] = item[value];
        if (item.operatingLevel == 'TEAM') {
          teamArr1.push(tempObj)
        } else {
          departmentArr1.push(tempObj)
        }
      });
    }
    return tempObj = {
      departmentArr: departmentArr1,
      teamArr: teamArr1
    };
  }
  lookOpLevel() {
    for (const i in this.orgList) {
      if (this.orgList[i].orgId == this.userManageBean.orgName || this.orgList[i].orgId == this.userManageBean.orgId) {
        this.operatingLevel = this.orgList[i].operatingLevel + '_DT';
        this.queryTeamId();
        return;
      }
    }
  }
  //onchange事件
  doChose(event) {
    this.userManageBean.orgName = this.returnValue1(event.value, 'label', 'value', this.list);
    this.lookOpLevel();
    this.orgFlag = true;
  }
  doChose1(event) {
    this.userManageBean.orgId = this.returnValue1(event.value, 'value', 'label', this.list);
    this.orgFlag = true;
  }
  doChose2(event) {
    this.userManageBean.departmentName = this.returnValue1(event.value, 'label', 'value', this.departmentName);
    this.departmentFlag = true;
  }
  doChose3(event) {
    this.userManageBean.departmentId = this.returnValue1(event.value, 'value', 'label', this.departmentName);
    this.departmentFlag = true;
  }
  doChose4(event) {
    this.userManageBean.teamName = this.returnValue1(event.value, 'label', 'value', this.teamName);
    this.teamFlag = true;
  }
  doChose5(event) {
    this.userManageBean.teamId = this.returnValue1(event.value, 'value', 'label', this.teamName);
    this.teamFlag = true;
  }

  /**
* 转换值
* @param param  值
* @param colum  字段
*/
  returnValue1(param, colum, valueColum, list) {
    let value: string = "";
    list.forEach(element => {
      if (element[colum] === param) {
        value = element[valueColum];
      }
    });
    return value;
  }

  //确定
  preserveClick() {
    //校验的
    for (const i in this.userform.controls) {
      this.userform.controls[i].markAsDirty();
    }
    //处理是否提交
    for (const i in this.userform.controls) {
      if (!this.userform.controls[i].valid) {
        return;
      }
    }

    if (this.orgFlag || this.departmentFlag || this.teamFlag) {
      if (this.orgFlag) {
        this.orgId1 = this.userManageBean.orgName;
        this.orgName1 = this.userManageBean.orgId;
      } else {
        this.orgId1 = this.userManageBean.orgId;
        this.orgName1 = this.userManageBean.orgName;
      }
      if (this.departmentFlag) {
        this.departmentId1 = this.userManageBean.departmentName;
        this.departmentName1 = this.userManageBean.departmentId;
      } else {
        this.departmentId1 = this.userManageBean.departmentId;
        this.departmentName1 = this.userManageBean.departmentName;
      }
      if (this.teamFlag) {
        this.teamId1 = this.userManageBean.teamName;
        this.teamName1 = this.userManageBean.teamId;
      } else {
        this.teamId1 = this.userManageBean.teamId;
        this.teamName1 = this.userManageBean.teamName;
      }
      this.param = {
        tellerId: this.userManageBean.tellerId, //行员号
        tellerName: this.userManageBean.tellerName,//行员姓名    
        tellerStatus: this.userManageBean.tellerStatus, //员工状态
        orgId: this.orgId1,//所属机构号
        orgName: this.orgName1,//所属机构名称
        departmentId: this.departmentId1,
        departmentName: this.departmentName1,
        teamId: this.teamId1,
        teamName: this.teamName1,
        email: this.userManageBean.email,//联系邮箱
        mobileNum: this.userManageBean.mobileNum,//联系电话
      }
      // return this.param
    } else {
      this.param = this.userManageBean
    }
    //提交服务

    this.commonHttpService.updateTellerInfo(this.param).subscribe(data => {
      if (data.returnCode.code == "success") {
        // this.userManageBean = new UserManageBean();
        this.outValue.emit(false);
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
        this.userManageBean.pageNum = '1';
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败" });
    })
  }
  //取消
  modifyCloseBtn() {
    this.outValue.emit(false)
  }
}

